@font-face {
  font-family: "Prompt-Light";
  src: url("../fonts/Prompt-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Ubuntu-Regular";
  src: url("../fonts/Ubuntu-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Ubuntu-Bold";
  src: url("../fonts/Ubuntu-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Prompt-Light", sans-serif;
  font-weight: 300;
  font-size: 18px;
  line-height: 110%;
  color: #FFFFFF;
}

.container {
  margin: 0 auto;
  max-width: 1285px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.logo__img {
  width: 191px;
  height: 28px;
}

.menu {
  display: flex;
}
.menu__item {
  margin-right: 65px;
  font-family: "Ubuntu-Regular", sans-serif;
  font-weight: 400;
  line-height: 21px;
  transition: 0.4s;
}
.menu__item:last-child {
  margin-right: 0;
}
.menu__item:hover {
  color: #0F95F6;
}

.button {
  padding: 10px 40px;
  font-family: "Ubuntu-Bold", sans-serif;
  font-weight: 700;
  line-height: 21px;
  border: 2px solid #FFFFFF;
  border-radius: 100px;
  transition: all 0.4s;
}
.button:hover {
  background-color: #0F95F6;
  border: 2px solid #0F95F6;
}

.btn-registration {
  padding: 10px 40px;
  background-color: #0F95F6;
  border-radius: 100px;
  font-family: "Ubuntu-Bold", sans-serif;
  font-weight: 700;
  line-height: 21px;
  transition: all 0.4s;
}
.btn-registration:hover {
  background-color: #FFFFFF;
  color: #111111;
}

.title {
  margin: 0;
  margin-bottom: 65px;
  font-family: "Ubuntu-Bold", sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 37px;
  color: #00255B;
}

.text {
  margin: 0;
}

